import React, { Component } from 'react';
import * as Yup from 'yup';
import {Formik, Form, Field} from 'formik';

import {auth} from '../../firebase';
import { createUserWithEmailAndPassword, updateProfile } from "firebase/auth";

import FormInput from './FormInput';

const passwordRegex = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/;

export default class FormSingUp extends Component {
    
    hendleSubmit = (values) => {
        createUserWithEmailAndPassword(auth, values.email, values.password).then(
            
            (user) => {updateProfile(auth.currentUser, {displayName: values.username})}
        );
    }
    render() {
        return <Formik initialValues={{ username:"", email:"", password:""}} onSubmit={this.hendleSubmit}

            validationSchema = {Yup.object().shape({
                username: Yup.string().required(' ').max(20, '     20 '),
                email: Yup.string().email('mail    ').required('mail '),
                password: Yup.string().matches(passwordRegex, '     8          ').required(' '),
            })}
        >
        { ({isValid}) => (
            <Form className='w-auto p-4 text-[18px]' >
                <Field name="username" component={FormInput} label=" "/>
                <Field name="email" component={FormInput} label=" mail"/>
                <Field name="password" component={FormInput} label=" " type="password"/>
                <button className=' m-2 border rounded border-gray-300 w-min px-2 hover:bg-amber-50' type="submit"  disabled={!isValid}></button>
            </Form>)}
        </Formik>
    }
}